import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import itemsService from '../services/items'
import { TextField, Button, Box, MenuItem, Select, makeStyles } from '@material-ui/core';

const useStyles = makeStyles({
    formInput: {
        marginLeft: '8px',
        marginRight: '8px'
    },
    submitButton: {
        position: 'relative',
        top: '20px'
    }
})

const EditItem = ({ item, items, setItems }) => {
    const classes = useStyles()
    const history = useHistory()
    const [description, setDescription] = useState('')
    const [category, setCategory] = useState('')
    const [brand, setBrand] = useState('')

    const updateItem = (event) => {
        event.preventDefault()
        const id = item.id
        const itemObject = { brand, description, category }
        itemsService.update(id, itemObject).then(updatedItem => {
            console.log(updatedItem)
            setItems(items.map(item => {
                if (item.id === id) {
                    return updatedItem
                }
                return item
            }))
            history.push('/items')
        }).catch(exception => {
            console.log(exception)
            if (exception.status === 401) {
                history.push('/login')
            } else if (exception.status === 400) {
                console.log(exception.data.error)
            }
        })

    }

    console.log(item)

    return (
        <form onSubmit={updateItem}>
            <Box my={1}>
                Brand:
                    <TextField className={classes.formInput} value={brand} onChange={(event) => setBrand(event.target.value)} placeholder={item.brand} />
            </Box>
            <Box my={1}>
                Description:
                    <TextField className={classes.formInput} value={description} onChange={(event) => setDescription(event.target.value)} placeholder={item.description} />
            </Box>
            <Box my={1}>
                Category:
                <Select className={classes.formInput} value={category} onChange={(event) => setCategory(event.target.value)} style={{ minWidth: 120 }}>
                    <MenuItem value="headwear">Headwear</MenuItem>
                    <MenuItem value="outerwear">Outerwear</MenuItem>
                    <MenuItem value="tops">Top</MenuItem>
                    <MenuItem value="bottoms">Bottom</MenuItem>
                    <MenuItem value="socks">Socks</MenuItem>
                    <MenuItem value="footwear">Footwear</MenuItem>
                </Select>
            </Box>
            <Button className={classes.submitButton} variant="contained" color="primary" type="submit">Update Item</Button>
        </form>
    )
}

export default EditItem