import React from 'react'
import EditItem from './EditItem'
import { Typography, makeStyles, Box } from '@material-ui/core';
import { Image } from 'cloudinary-react'

const useStyles = makeStyles({
    itemImage: {
        margin: '5px'
    }
})

const Item = ({ item, items, setItems }) => {
    const classes = useStyles()
    if (!item) {
        return null
    }

    return (
        <Box my={3}>
            <Typography variant="h3" align="center">Edit Item</Typography>
            <Box display="flex" justifyContent="center">
                {item.imageID && <Image className={classes.itemImage} publicId={item.imageID} cloudName="ddm0yp9dq" />}
                <EditItem item={item} items={items} setItems={setItems} />
            </Box>
        </Box>
    )
}

export default Item