import React, { useState } from 'react'
import tokenHelper from '../services/tokenHelper'
import loginService from '../services/login'
import { makeStyles, Button, Box, TextField, Typography, Grid } from '@material-ui/core'
import NotificationSnackbar from '../components/NotificationSnackbar'


const useStyles = makeStyles({
    root: {
        minHeight: '50vh',
        color: '#1e1e24'
    },
    form: {
        border: '1px solid #cdcdcd',
        padding: '40px',
        background: '#fefcfb'
    }
})

const Login = ({ setUser }) => {
    const classes = useStyles()
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [showNotification, setShowNotification] = useState(false)
    const [showErrorNotification ,setShowErrorNotification] = useState(false)
    const [message, setMessage] = useState('')

    const onSubmit = (event) => {
        event.preventDefault()
        loginService.login({ username, password }).then(response => {
            if (response.status === 200) {
                let user = response.data
                tokenHelper.setToken(user.token)
                window.localStorage.setItem(
                    'loggedInUser', JSON.stringify(user)
                )
                setUser(user)
            }
        }).catch(exception => {
            setMessage('Incorrect credentials, please try again')            
            setShowErrorNotification(true)
            setTimeout(() => {
                setShowErrorNotification(false)
            }, 4000)
        })

    }

    return (
        <Grid container justify="center" alignItems="center" className={classes.root}>
            <form onSubmit={onSubmit} className={classes.form}>
                <Grid container direction="column" alignItems="center">
                    <Typography variant="h6">Virtual Wardrobe</Typography>
                    <Box my={1}>
                        <TextField variant="outlined" value={username} label="Username" onChange={({ target }) => setUsername(target.value)} />
                    </Box>
                    <Box my={1}>
                        <TextField variant="outlined" type="password" value={password} label="Password" onChange={({ target }) => setPassword(target.value)} />
                    </Box>
                    <Box mt={2}>
                        <Button variant="contained" color="primary" type="submit">login</Button>
                    </Box>
                </Grid>
            </form>
            <NotificationSnackbar showNotification={showNotification} setShowNotification={setShowNotification} type='success' message={message} />
            <NotificationSnackbar showNotification={showErrorNotification} setShowNotification={setShowErrorNotification} type='error' message={message} />
        </Grid>
    )
}

export default Login