import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import outfitService from '../services/outfits'
import { Image } from 'cloudinary-react'
import { Grid, Typography } from '@material-ui/core';


const Outfit = ({ match, outfit }) => {
    const [retrieveOutfit, setRetrieveOutfit] = useState({})
    const history = useHistory()
    useEffect(() => {
        if (!outfit) {
            outfitService.getOne(match.params.id).then(retrievedOutfit => {
                setRetrieveOutfit(retrievedOutfit)
            }).catch(exception => {
                console.log(exception)
                history.push('/')
            })
        }
    }, [history, match.params.id, outfit])

    if (outfit) {
        return (
            <Grid container direction="column" alignItems="center">
                <Typography variant="h6">Outfit ID: {outfit.id}</Typography>
                {outfit.headwear && <Image publicId={outfit.headwear.imageID} cloudName="ddm0yp9dq" />}<br />
                {outfit.outerwear && <Image publicId={outfit.outerwear.imageID} cloudName="ddm0yp9dq" />}<br />
                {outfit.top && <Image publicId={outfit.top.imageID} cloudName="ddm0yp9dq" />}<br />
                {outfit.bottom && <Image publicId={outfit.bottom.imageID} cloudName="ddm0yp9dq" />}<br />
                {outfit.socks && <Image publicId={outfit.socks.imageID} cloudName="ddm0yp9dq" />}<br />
                {outfit.footwear && <Image publicId={outfit.footwear.imageID} cloudName="ddm0yp9dq" />}<br />
            </Grid>

        )
    }

    return (
        <Grid container direction="column" alignItems="center">
            <Typography variant="h6">Outfit ID: {retrieveOutfit.id}</Typography>
            {retrieveOutfit.headwear && <Image publicId={retrieveOutfit.headwear.imageID} cloudName="ddm0yp9dq" />}
            {retrieveOutfit.outerwear && <Image publicId={retrieveOutfit.outerwear.imageID} cloudName="ddm0yp9dq" />}
            {retrieveOutfit.top && <Image publicId={retrieveOutfit.top.imageID} cloudName="ddm0yp9dq" />}
            {retrieveOutfit.bottom && <Image publicId={retrieveOutfit.bottom.imageID} cloudName="ddm0yp9dq" />}
            {retrieveOutfit.socks && <Image publicId={retrieveOutfit.socks.imageID} cloudName="ddm0yp9dq" />}
            {retrieveOutfit.footwear && <Image publicId={retrieveOutfit.footwear.imageID} cloudName="ddm0yp9dq" />}
        </Grid>
    )
}

export default Outfit